<script lang="js">
import { mapGetters } from 'vuex'
import * as d3 from "d3"
import { drawParallel } from "@/assets/js/parallel.js"
import { drawColumn } from "@/assets/js/drawcolumn.js"
import { init } from "@/assets/js/3d.js"
export default {
    data() {
      return {
        columnindex: 0,
        rvalue: 0,
        oriimg: 'dataset/img/origin.jpeg',
        diffimg: "./dataset/img/svd_D_K_10.png",
        compimg: "./dataset/img/svd_R_K_10.png",
        defaultlist: [5, 10, 15, 20, 30, 40, 50, 60, 70, 80, 90, 100, 150, 200]
        // originimg: "./dataset/img/origin.jpeg"
      };
    },
    computed: {
        ...mapGetters([
            'pageIndex'
        ]),
    },
    mounted(){
      d3.csv('./dataset/proj.csv').then(function(data) {
        drawParallel(data, "1")
        // console.log(parcoords)
        // parcoords.on("brushend", function(brushed, args){
        //   brushed_data = getBrushedData(args.axis, args.selection.scaled, )  
        //   })
        setTimeout(() => {
          //let container = d3.select('')
          d3.selectAll('.axis').each(function(d,i) {
            console.log(d, i)
            let bbox = this.getBoundingClientRect()
            let div = d3.select('.top').append('div')
              .style('top', `${bbox.top + 15}px`)
              .style('left', `${bbox.left + 40 }px`)
              .style('position', 'absolute')
              .style('transform', 'rotate(90deg)')
            div.append('div')
              .style('position', 'absolute')
               .append('img')
              .attr('src', `./dataset/img/comp_${i+1}.png`)
              .style('width', `${bbox.height-15}px`)
              .style('height', `${15}px`)
         
              
              
          })
        }, 0);
       })
      let self = this
      d3.json("./dataset/data/svd_R_K_5.json").then(function(data) {
        console.log(data.length, "abgahj")
        let col_index = self.columnindex;
        // if (error) throw error;
        drawColumn(data, col_index)
        init(data[col_index]);
    })
      },
    methods: {
      handleSlider(value) {
      console.log(value)
      this.diffimg = "./dataset/img/svd_D_K_" + this.defaultlist[value] + ".png"
      this.compimg = "./dataset/img/svd_R_K_" + this.defaultlist[value] + ".png"
        let filepath = "./dataset/data/svd_R_K_" + this.defaultlist[value] + ".json"
      console.log(filepath)
      let self = this
      d3.json(filepath).then(function(data) {
        let col_index = self.columnindex;
        // if (error) throw error;
        drawColumn(data, col_index)
        init(data[col_index]);
    })
        },
      handleColumn(value) {
      let filepath = "./dataset/data/svd_R_K_" + this.defaultlist[this.rvalue] + ".json"
      console.log(filepath)
      d3.json(filepath).then(function(data) {
        console.log("here")
        let col_index = value;
        // if (error) throw error;
        drawColumn(data, col_index)
        init(data[col_index]);
    })
        },
      formatTooltip(val) {
        return this.defaultlist[val];
      }
    },
    watch: {
    }
  }
</script>
