import * as d3 from 'd3'
import ParCoords from 'parcoord-es'
let drawParallel = function(data, target_var){
    // let target_var = "quality"
    console.log(target_var, "in_parallel")
    let target_values = data.map(function(d){return d[target_var]})
    // let variables = data.columns
    // for(var i = 0; i < data.length; i++){
    //     data[i].id = i
    // }
    let allvariable = Object.keys(data[0])
    let variables = allvariable.filter((d) => !(d.includes("shap-")))

    var colorScale = d3.scaleLinear()
    // .domain([d3.max(d => d.quality), d3.median(d => d.quality), d3.min(d => d.quality)])
    .domain([d3.min(target_values), d3.median(target_values), d3.max(target_values)])
    .range(["#000080", "#f0f0f0", "#B22222"])
    .interpolate(d3.interpolateLab);

    var dimensions = {}
    // variables = variables.slice(0, 5)
    variables.forEach(d => {
        dimensions[d] = {}
    });
    let div = d3.select('#parallel')
    let bbox = div.node().getBoundingClientRect()
    let width = Math.ceil(bbox.width)
    let height = Math.ceil(bbox.height)
    var parcoords = ParCoords()("#parallel")
    .alpha(0.3)
    .mode("queue") // progressive rendering
    .height(height)
    .width(width)
    .margin({
      top: 20,
      left: 25,
      right: 10,
      bottom: 10
    });
    console.log("parallel", data[0]["type"])
    parcoords.data(data)
    .dimensions(dimensions)
    .color(function(d) { return colorScale(d[target_var]); })  // quantitative color scale
    .render()
    // .brushMode("1D-axes-multi")
    // .reorderable()
    .brushMode("1D-axes")
    return parcoords

}
export {drawParallel}