import * as d3 from "d3"
import { _3d } from 'd3-3d' ;
var origin = [300, 220], j = 10, scale = 18, scatter = [], yLine = [], xGrid = [], beta = 0, alpha = 0,  startAngle = Math.PI/4;
let svg
//   .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

var mx=0, my=0, mouseX=0, mouseY=0;

var grid3d = _3d()
    .shape('GRID', 20)
    .origin(origin)
    .rotateY( startAngle)
    .rotateX(-startAngle)
    .scale(scale);

var point3d = _3d()
    .x(function(d){ return d.x; })
    .y(function(d){ return d.y; })
    .z(function(d){ return d.z; })
    .origin(origin)
    .rotateY( startAngle)
    .rotateX(-startAngle)
    .scale(scale);

var yScale3d = _3d()
    .shape('LINE_STRIP')
    .origin(origin)
    .rotateY( startAngle)
    .rotateX(-startAngle)
    .scale(scale);

function processData(data){

    /* ----------- GRID ----------- */

    var xGrid = svg.selectAll('path.grid').data(data[0]);
    console.log(xGrid, "12345")
    xGrid
        .enter()
        .append('path')
        .attr('class', '_3d grid')
        .merge(xGrid)
        .attr('stroke', 'black')
        .attr('stroke-width', 0.3)
        .attr('fill', function(d){ return d.ccw ? 'lightgrey' : '#717171'; })
        .attr('fill-opacity', 0.9)
        .attr('d', grid3d.draw);

    xGrid.exit().remove();

    /* ----------- POINTS ----------- */

    var points = svg.selectAll('circle').data(data[1]);

    points
    .enter()
    .append('circle')
    .attr('class', '_3d')
    .attr('opacity', 0)
    .attr('cx', function(d) {return posPointX(d);})
    .attr('cy', function(d) {return posPointY(d);})
    .merge(points)
    // .transition().duration(tt)
    .attr('r', d => 3 + d.group)
    .attr('stroke', function(d){ 
        return d3.rgb(d.color[0],d.color[1],d.color[2]).darker(d.group); })
    .attr('fill', function(d){ return d3.rgb(d.color[0],d.color[1],d.color[2]); })
    .attr('opacity',d => 0.7 + 0.3*(d.group))
    .attr('cx', function(d) {return posPointX(d);})
    .attr('cy', function(d) {return posPointY(d);});


    
    points.exit().remove();

    /* ----------- y-Scale ----------- */

    var yScale = svg.selectAll('path.yScale').data(data[2]);

    yScale
        .enter()
        .append('path')
        .attr('class', '_3d yScale')
        .merge(yScale)
        .attr('stroke', 'black')
        .attr('stroke-width', .5)
        .attr('d', yScale3d.draw);

    yScale.exit().remove();

     /* ----------- y-Scale Text ----------- */

    var yText = svg.selectAll('text.yText').data(data[2][0]);

    yText
        .enter()
        .append('text')
        .attr('class', '_3d yText')
        .attr('dx', '.3em')
        .merge(yText)
        .each(function(d){
            d.centroid = {x: d.rotated.x, y: d.rotated.y, z: d.rotated.z};
        })
        .attr('x', function(d){ return d.projected.x; })
        .attr('y', function(d){ return d.projected.y; })
        .text(function(d){ return d[1] <= 0 ? d[1] : ''; });

    yText.exit().remove();

    d3.selectAll('._3d').sort(_3d().sort);
}

function posPointX(d){
    return d.projected.x;
}

function posPointY(d){
    return d.projected.y;
}




function dragStart(event){
    mx = event.x;
    my = event.y;
}

function dragged(event){
    mouseX = mouseX || 0;
    mouseY = mouseY || 0;
    beta   = (event.x - mx + mouseX) * Math.PI / 230 ;
    alpha  = (event.y - my + mouseY) * Math.PI / 230  * (-1);
    var data = [
         grid3d.rotateY(beta + startAngle).rotateX(alpha - startAngle)(xGrid),
        point3d.rotateY(beta + startAngle).rotateX(alpha - startAngle)(scatter),
        yScale3d.rotateY(beta + startAngle).rotateX(alpha - startAngle)([yLine]),
    ];
    processData(data, 0);
}

function dragEnd(event){
    mouseX = event.x - mx + mouseX;
    mouseY = event.y - my + mouseY;
}

function init(data){
    console.log(data, "123")
    let div = d3.select('#threeddiv').html('')
    let bbox = div.node().getBoundingClientRect()
    let width = Math.ceil(bbox.width)
    let height = Math.ceil(bbox.height)
    // let margin = {top: height * 0.05, right: width * 0.05, bottom: height * 0.05, left: width * 0.05}
    svg = div.append("svg")
    .attr("id", "threedplot")
    .attr("width", width)
    .attr("height", height)
    .call(d3.drag().on('drag', dragged).on('start', dragStart).on('end', dragEnd));

    console.log(svg, "svg")
    xGrid = [], scatter = [], yLine = [];
    for(var z = -j; z < j; z++){
        for(var x = -j; x < j; x++){
            xGrid.push([x, 1, z]);
        }
    }
    for (var i=0;i<data.length;i++)
    { 
        // scatter.push({x: -data[i].p[0]/25 *2 +j, y: -data[i].p[1]/25, z: -data[i].p[2]/25 *2 +j});
        scatter.push({x: -data[i].p[0]/25.5 *2 +j, y: -data[i].p[2]/25.5, z: -data[i].p[1]/25 *2 +j,
            color:[data[i].f[2], data[i].f[1], data[i].f[0]], group: 1
        });
        scatter.push({x: -data[i].f[0]/25.5 *2 +j, y: -data[i].f[2]/25.5, z: -data[i].f[1]/25 *2 +j,
            color:[data[i].f[2], data[i].f[1], data[i].f[0]], group: 0
        });
    }
    d3.range(-1, 11, 1).forEach(function(d){ yLine.push([-j, -d, -j]); });
    
    let prodata = [
        grid3d(xGrid),
        point3d(scatter),
        yScale3d([yLine])
    ];
    processData(prodata, 1000);
}

export {init}