// import pixelate from 'pixelate'
// import drawSvg from 'drawSvg'
import * as d3 from "d3"
function drawColumn(data, col_index){
  const canvas = document.querySelector('#mycanvas')
  const context = canvas.getContext('2d')
  const srcImg = document.getElementById('image')
  const image = new Image()
  image.src = srcImg.src
  image.onload = function () {
    const width = 33 * 3;
    const height = 415;
    // Render the image on the canvas.
    context.drawImage(image, 0, 0)
    // Create main SVG element.
    const svg = d3.select('#pixelated').html("")
    .append('svg')
      .attr('width', width)
      .attr('height', height)
      .attr('viewBox', '0 0 80 840')
      .append('g')

  
    const squaresGroup = svg.append('g')
      .attr('class', 'square-g')
      const start = Math.max(0,col_index - 15);
      const end = Math.min(539,col_index + 15);
      var cols = [];
      for(var i=start;i<=end;i++){
        cols.push(i);
      }
      var drawd = [];
      for (let x = 0; x <=cols.length; x++) {
        let col = cols[x];
        drawd.push.apply(drawd,data[col]);
        }
      // console.log(drawd);
  
      
      drawSvg({
        colors: drawd,
        col_offset: start,
        svg: squaresGroup,
        col_index: col_index,
      })

}


function drawSvg(props) {
  props = {
    colors: null, // []
    svg: null,
    ...props
  }
  const {colors, col_offset, svg, col_index} = props
  console.log('colors', colors)
  const squares = svg.selectAll('.square').data(colors)
  squares.exit().remove()
  squares.enter().append('rect')
    .attr('class', 'square')
    .merge(squares)
    .attr('y', d => d.x)
    .attr('x', d => (d.y - col_offset)*3)
    .attr('width', 3)
    .attr('height', 1)
    .attr('fill', d => d3.rgb(d.p[2],d.p[1],d.p[0]))
    .attr('opacity',d =>0.3 + 0.7*(d.y == col_index))

}
}

export { drawColumn }